"use client"
import React, { useState, useEffect } from "react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";

interface Peserta {
  nama: string;
  kelas: string;
  aksi: string;
  tanggal: string;
  poin: number;
}

interface GaleriItem {
  nama: string;
  deskripsi: string;
  file: string;
}

interface TantanganItem {
  id: number;
  judul: string;
  deskripsi: string;
  selesai: boolean;
}

interface AgendaItem {
  tanggal: string;
  kegiatan: string;
}

export default function GercabGO() {
  const [peserta, setPeserta] = useState<Peserta[]>([]);
  const [galeri, setGaleri] = useState<GaleriItem[]>([]);
  const [tantangan, setTantangan] = useState<TantanganItem[]>([]);
  const [agenda, setAgenda] = useState<AgendaItem[]>([]);

  const [nama, setNama] = useState("");
  const [kelas, setKelas] = useState("");
  const [aksi, setAksi] = useState("");
  const [file, setFile] = useState<string>("");

  useEffect(() => {
    const savedPeserta = JSON.parse(localStorage.getItem("peserta") || "[]");
    const savedGaleri = JSON.parse(localStorage.getItem("galeri") || "[]");
    const savedTantangan = JSON.parse(localStorage.getItem("tantangan") || "[]");
    const savedAgenda = JSON.parse(localStorage.getItem("agenda") || "[]");
    setPeserta(savedPeserta);
    setGaleri(savedGaleri);
    setTantangan(savedTantangan);
    setAgenda(savedAgenda);
  }, []);

  const simpanJejak = () => {
    const poin = 10; // poin default per aksi
    const tanggal = new Date().toLocaleDateString();
    const baru = [...peserta, { nama, kelas, aksi, poin, tanggal }];
    setPeserta(baru);
    localStorage.setItem("peserta", JSON.stringify(baru));
    setAksi("");
  };

  const unggahGaleri = () => {
    const reader = new FileReader();
    const input = document.getElementById("fileInput") as HTMLInputElement;
    if (input.files && input.files[0]) {
      reader.onload = () => {
        const baru = [...galeri, { nama, deskripsi: aksi, file: reader.result as string }];
        setGaleri(baru);
        localStorage.setItem("galeri", JSON.stringify(baru));
      };
      reader.readAsDataURL(input.files[0]);
    }
  };

  const toggleTantangan = (id: number) => {
    const update = tantangan.map(t => t.id === id ? { ...t, selesai: !t.selesai } : t);
    setTantangan(update);
    localStorage.setItem("tantangan", JSON.stringify(update));
  };

  return (
    <Tabs defaultValue="jejak" className="w-full">
      <TabsList className="grid grid-cols-4 sm:grid-cols-6 w-full bg-green-600 rounded-xl mb-4 text-white">
        <TabsTrigger value="jejak">Jejak Hijau</TabsTrigger>
        <TabsTrigger value="galeri">Galeri</TabsTrigger>
        <TabsTrigger value="tantangan">Tantangan</TabsTrigger>
        <TabsTrigger value="agenda">Agenda</TabsTrigger>
      </TabsList>

      <TabsContent value="jejak" className="space-y-4 mt-4">
        <h2 className="text-lg font-bold text-green-800">Catat Aksi Hijau</h2>
        <Input placeholder="Nama" value={nama} onChange={e => setNama(e.target.value)} />
        <Input placeholder="Kelas" value={kelas} onChange={e => setKelas(e.target.value)} />
        <Input placeholder="Aksi Lingkungan" value={aksi} onChange={e => setAksi(e.target.value)} />
        <Button onClick={simpanJejak}>Simpan</Button>
        <h3 className="font-bold mt-6">Leaderboard</h3>
        {peserta.sort((a, b) => b.poin - a.poin).map((p, i) => (
          <Card key={i} className="p-2">
            <CardContent>
              {i + 1}. {p.nama} ({p.kelas}) - {p.poin} poin - {p.aksi}
            </CardContent>
          </Card>
        ))}
      </TabsContent>

      <TabsContent value="galeri" className="space-y-4 mt-4">
        <h2 className="text-lg font-bold text-green-800">Unggah Dokumentasi Budaya</h2>
        <Input placeholder="Nama" value={nama} onChange={e => setNama(e.target.value)} />
        <Input placeholder="Deskripsi" value={aksi} onChange={e => setAksi(e.target.value)} />
        <Input id="fileInput" type="file" accept="image/*" />
        <Button onClick={unggahGaleri}>Unggah</Button>
        <div className="grid grid-cols-2 md:grid-cols-3 gap-4 mt-4">
          {galeri.map((item, i) => (
            <Card key={i}>
              <CardContent>
                <img src={item.file} alt="galeri" className="w-full h-32 object-cover rounded" />
                <p className="font-bold mt-2">{item.nama}</p>
                <p className="text-sm">{item.deskripsi}</p>
              </CardContent>
            </Card>
          ))}
        </div>
      </TabsContent>

      <TabsContent value="tantangan" className="space-y-4 mt-4">
        <h2 className="text-lg font-bold text-green-800">Tantangan GERCAB</h2>
        {tantangan.length === 0 && (
          <Button onClick={() => {
            const contoh = [
              { id: 1, judul: "Bawa Botol Minum Sendiri", deskripsi: "Kurangi sampah plastik!", selesai: false },
              { id: 2, judul: "Menanam 1 Pohon", deskripsi: "Hijaukan lingkungan sekitar", selesai: false }
            ];
            setTantangan(contoh);
            localStorage.setItem("tantangan", JSON.stringify(contoh));
          }}>Muat Tantangan</Button>
        )}
        {tantangan.map((t, i) => (
          <Card key={i}>
            <CardContent>
              <div className="flex items-center justify-between">
                <div>
                  <p className="font-bold">{t.judul}</p>
                  <p className="text-sm text-gray-500">{t.deskripsi}</p>
                </div>
                <Button variant={t.selesai ? "secondary" : "default"} onClick={() => toggleTantangan(t.id)}>
                  {t.selesai ? "Selesai âœ…" : "Selesaikan"}
                </Button>
              </div>
            </CardContent>
          </Card>
        ))}
      </TabsContent>

      <TabsContent value="agenda" className="space-y-4 mt-4">
        <h2 className="text-lg font-bold text-green-800">Agenda Kegiatan</h2>
        {agenda.length === 0 ? (
          <p>Belum ada agenda.</p>
        ) : (
          agenda.map((a, i) => (
            <Card key={i}>
              <CardContent>
                <p className="font-bold">{a.tanggal}</p>
                <p>{a.kegiatan}</p>
              </CardContent>
            </Card>
          ))
        )}
      </TabsContent>
    </Tabs>
  );
}
